"use server";

import { db } from "@/lib/prisma";
import { auth } from "@clerk/nextjs/server";
import { GoogleGenerativeAI } from "@google/generative-ai";

const genAI = new GoogleGenerativeAI(process.env.GEMINI_API_KEY);
const model = genAI.getGenerativeModel({ model: "gemini-1.5-flash" });

async function getUser() {
  const { userId } = await auth();
  if (!userId) throw new Error("Unauthorized");

  const user = await db.user.findUnique({
    where: { clerkUserId: userId },
  });
  if (!user) throw new Error("User not found");

  return user;
}

export async function generateCoverLetter({ jobTitle, companyName, jobDescription }) {
  const user = await getUser();

  const prompt = `
Write a professional cover letter for a ${jobTitle} position at ${companyName}.

About the candidate:
- Industry: ${user.industry}
- Years of Experience: ${user.experience}
- Skills: ${user.skills?.join(", ")}
- Professional Background: ${user.bio}

Job Description:
${jobDescription}

Requirements:
1. Professional, enthusiastic tone
2. Highlight relevant skills and experience
3. Show understanding of the company's needs
4. Max 400 words
5. Proper business letter formatting in markdown
6. Include achievements
7. Relate background to job requirements
`;

  try {
    const result = await model.generateContent(prompt);
    const content = result.response.text().trim();

    return await db.coverLetter.create({
      data: {
        content,
        jobTitle,
        companyName,
        jobDescription,
        status: "completed",
        userId: user.id,
      },
    });
  } catch (error) {
    console.error("Error generating cover letter:", error.message);
    throw new Error("Failed to generate cover letter");
  }
}

export async function getCoverLetters() {
  const user = await getUser();
  return await db.coverLetter.findMany({
    where: { userId: user.id },
    orderBy: { createdAt: "desc" },
  });
}

export async function deleteCoverLetter(id) {
  const user = await getUser();
  return await db.coverLetter.delete({
    where: { id, userId: user.id },
  });
}
