"use client";

import { useState } from "react";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Loader2 } from "lucide-react";
import { toast } from "sonner";
import { generateCoverLetter } from "@/actions/coverletter";

export default function AICoverLetter() {
  const [input, setInput] = useState("");
  const [output, setOutput] = useState("");
  const [loading, setLoading] = useState(false);

  const handleGenerate = async () => {
    if (!input.trim()) {
      toast.error("Please enter some job description or details.");
      return;
    }

    setLoading(true);
    try {
      const result = await generateCoverLetter(input);
      setOutput(result);
      toast.success("Cover letter generated!");
    } catch (error) {
      console.error(error);
      toast.error("Failed to generate cover letter.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6 p-6 max-w-4xl mx-auto">
      <h1 className="text-4xl font-bold gradient-title text-center">
        AI Cover Letter Generator
      </h1>

      <div className="space-y-2">
        <label className="font-medium text-lg">Job / Candidate Details</label>
        <Textarea
          placeholder="Enter job description, skills, or candidate info..."
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="h-40"
        />
      </div>

      <Button onClick={handleGenerate} disabled={loading}>
        {loading ? (
          <>
            <Loader2 className="h-4 w-4 animate-spin mr-2" />
            Generating...
          </>
        ) : (
          "Generate Cover Letter"
        )}
      </Button>

      {output && (
        <div className="border rounded-lg p-4 bg-muted/50">
          <h2 className="font-semibold text-lg mb-2">Generated Cover Letter</h2>
          <pre className="whitespace-pre-wrap">{output}</pre>
        </div>
      )}
    </div>
  );
}
